const validator = {
	nCard: {
		rules: [
			{
				test: /^(?:5[1-5][0-9]{2}|222[1-9]|22[3-9][0-9]|2[3-6][0-9]{2}|27[01][0-9]|2720)[0-9]{12}$/,
				message: 'Nmero invlido.'
			}
		],
		errors: [],
		valid: false,
		state: '',
		mask: '9999-9999-9999-9999'
	},
	valCard: {
		rules: [
			{
				test: /^[0-9]{2}\/[0-9]{4}\//,
				message: 'Data de vencimento invlida.'
			}
		],
		errors: [],
		valid: false,
		state: '',
		mask: '99/9999'
	}
};

export default validator;



