import React from "react";
import { Button, Row, Col, Card, CardBody, CardTitle, Form } from "reactstrap";
import InputBanky from "../../../components/input-banky/input-banky";
import Validator from "./validator";
import cardTemplate from "../../../assets/images/bank-card.svg";

const cardStyle = {
  lineHeight: "0"
};

class YourCard extends React.Component {
  state = {
    cardStatus: false,
    step: 0,
    step1: {
      nCard: "",
      valCard: ""
    }
  };

  constructor(props) {
    super(props);
    this.validators = Validator;
  }

  handleComponentChanges = input => e => {
    const { step1 } = this.state;
    step1[input] = e.target.value;
    this.setState({ step1 });
  };

  activeCard = () => {
    this.setState(prevStep => {
      this.setState(prevStep + 1);
    });
  };

  cardIsInactive = () => {
    const { step } = this.state;
    switch (step) {
      case 0:
        return this.cardIsInactiveStep0();
      case 1:
        return this.cardIsInactiveStep1();
      default:
        return this.cardIsInactiveStep0();
    }
  };

  cardIsInactiveStep0 = () => (
    <Card className="not-received-card">
      <CardBody>
        <CardTitle className="section-card-title">
          <i className="" />
          Voc recebeu seu carto?
        </CardTitle>
        <Row className="align-items-center">
          <Col xs="12" className="mb-2 text-justify">
            Para desbloquear seu carto  simples. Basta clicar no boto abaixo,
            preencher as informaes solicitados e pronto. Seu carto est
            pronto para ser utilizado.
          </Col>
          <Col xs="12" className="mt-2">
            <Button color="primary" onClick={this.activeCard}>
              Quero ativar meu carto
            </Button>
          </Col>
        </Row>
      </CardBody>
    </Card>
  );

  cardIsInactiveStep1 = () => {
    const {
      step1: { nCard, valCard }
    } = this.state;
    return (
      <CardBody className="not-received-card">
        <CardTitle className="section-card-title">Ativar Carto</CardTitle>
        <Row className="align-items-center">
          <Col xs="12" className="mb-2 text-justify">
            Digite as informaes abaixo para realizar a ativao do seu carto
            Banky
          </Col>
          <Col xs="12" className="">
            {/*      */}
            {/* Form */}
            {/*      */}
            <Form className="form-material mb-auto">
              <Row className="justify-content-center align-content-top">
                <Col xs="12" className="mx-auto">
                  <InputBanky
                    label="Nmero do Carto"
                    type="text"
                    onChange={this.handleComponentChanges("nCard")}
                    value={nCard}
                    name="nCard"
                    mask={this.validators.nCard.mask}
                    formNoValidate
                    required
                  />
                  {this.showErrors("nCard")}
                </Col>
              </Row>
              <Row className="justify-content-center align-content-top">
                <Col xs="12" className="mx-auto">
                  <InputBanky
                    label="Validade (MM/AAAA)"
                    type="text"
                    onChange={this.handleComponentChanges("valCard")}
                    value={valCard}
                    name="valCard"
                    mask={this.validators.valCard.mask}
                    formNoValidate
                    required
                  />
                  {this.showErrors("valCard")}
                </Col>
              </Row>
            </Form>
          </Col>
          <Col xs="12" className="mt-2">
            <Button color="primary" onClick={this.activeCard}>
              Quero ativar meu carto
            </Button>
          </Col>
        </Row>
      </CardBody>
    );
  };

  cardIsActive = () => (
    <CardBody>
      <Row className="justify-content-around align-items-start">
        <Col xs="12" className="">
          <div className="d-flex flex-wrap">
            <div>
              <CardTitle className="section-card-title">
                <i className="icon-wallet" />
                Seu carto
              </CardTitle>
            </div>
          </div>
        </Col>
        <Col xs="auto" className="text-center">
          <Card className="balance-card">
            <CardBody>
              <Row className="align-items-center">
                <Col xs="12" className="mb-2">
                  <span>Saldo atualizado as 10:45</span>
                </Col>
                <Col xs="12" className="my-2">
                  <span className="display-6 text-success my-auto">
                    R$ 35,28
                  </span>
                </Col>
                <Col xs="12" className="mt-2">
                  <span className="text-muted">Gasto no ms: R$ 300,45</span>
                </Col>
              </Row>
            </CardBody>
          </Card>
        </Col>
        <Col xs="auto" className="text-center mb-3">
          <img
            className=""
            src={cardTemplate}
            style={cardStyle}
            alt="Seu carto"
            height="160"
          />
        </Col>
      </Row>
    </CardBody>
  );

  showErrors(fieldName) {
    if (fieldName === "") {
      return <div className="error" />;
    }
    const validator = this.validators[fieldName];

    if (validator && !validator.valid) {
      const errors = validator.errors.map((info, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <span className="error" key={index}>
          <i className="mdi mdi-alert-octagram" />
          {info}
        </span>
      ));
      return <div className="error">{errors}</div>;
    }
    return <div className="error" />;
  }

  render() {
    const { cardStatus } = this.state;
    return (
      <Card id="yourCardComponent">
        {cardStatus ? this.cardIsActive() : this.cardIsInactive()}
      </Card>
    );
  }
}

export default YourCard;



